import DataService from '../../../services/DataService';

let leftsearchSchema = [
    {
        fieldName: 'Transaction Reference No.',
        fieldType: "text",
        fieldIcon: "fa-hand-holding-usd",
        fieldSearchConditionName: ['CAST(TBPR.TRANSACTION_REF_ID AS NVARCHAR(50))'],
        isSearch: false,
        value: "",
        listOptions: []
    },
    {
        fieldName: 'CREATED_AT',
        fieldType: "date",
        fieldIcon: "fa-calendar",
        fieldSearchConditionName: ['TCWT.CREATED_AT'],
        isSearch: false,
        value: "",
        listOptions: [],
        datePickerConfig: {
            formate: 'yyyy-mm-dd',
        },
    },
    {
        fieldName: 'Transaction Type',
        fieldType: "ddl-check",
        fieldIcon: "fa fa-user-tie",
        fieldSearchConditionName: ['TCWT.TXN_TYPE'],
        isSearch: false,
        value: "",
        listOptions: []
    },

]

function bindleftCommonSearchdropdown(instance, FilterName) {
    debugger
    var vm = instance;


    var params = `Type=${FilterName}`

    vm.isLoading = true;

    DataService.TransactionListingFieldData(params).then(response => {

        if (response.data !== null) {

            var json = response.data;

            if (FilterName == 'Transaction_Type') {
                vm.leftsearchSchema[2].listOptions = json.map((item) => {
                    const container = {};
                    container.name = item.name;
                    container.value = item.id.toString();
                    return container;
                })

            }


        }
    })

}

export default { leftsearchSchema, bindleftCommonSearchdropdown };