<template>
    <section>

         <tg-list :IsShowAction="false" ref="tgList" :showCheckBox="false" ModuleName="Client" SubModuleCode="Billing"
            IdentityColumn="TICKET_ID" :HeaderText="$t('Wallet History')" :ListData="HistoryData" :listType="['List']" 
            :HeaderData="Headers" :NorecordfoundText="$t('NoRecordfound')" :TotalRecords="TotalRecords" 
            :SearchFields="leftsearchSchema" RenderRowActionMethod="() => { }" :ListDataCallBackFunction="FetchHistoryRecords" 
            :LegendArray="LegendArray"
            :HeaderButtons="listheaderbuttons" :CurrentPageNumber="PageNumber"
            :widgets="[]" v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick"
            v-on:SortdataButtonClick="sortdata"> 
            <template v-slot:slotdata="{ data }">
                 <template v-if="data.column.COLUMN_NAME == 'CREATED_AT'">
                    <span>
                        {{ $options.filters.formatDateTime(toUserDateTime(data.row.CREATED_AT,data.row.TIMEZONE)) }}
                    </span>
                </template> 
                <template v-if="data.column.COLUMN_NAME == 'AMOUNT'">
                    <span :class="data.row.TXN_TYPE === 'CREDIT' ?  'text-success' :'text-danger'">
                          {{ data.row.TXN_TYPE === 'CREDIT' ? '+' : '-' }}{{ $n(data.row.AMOUNT, 'currency', $getLocaleByCurrency(Currency)) }}
                    </span>
                </template> 
                <template v-if="data.column.COLUMN_NAME == 'AFTER_BALANCE'">
                    <span>
                        {{ $n(data.row.AFTER_BALANCE, 'currency', $getLocaleByCurrency(Currency)) }}
                    </span>
                </template> 
            </template>
        </tg-list>
    </section>
</template>

<script>
import DataService from '../../../services/DataService';
import ClientWalletTransactionHistory from './ClientwalletTransactionHistory.js';
export default {
    data() {
        return {
            isLoading: false,
            Currency: '',
            TotalRecords: 0,
            HistoryData: [],
            isHidden: false,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            GroupStart: 1,
            GroupEnd: 3,
            CurrentPage: 1,
            TotalPages: 0,
            Headers: [
                {
                "COLUMN_NAME": "TRANSACTION_REFERENCE_NUMBER",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Transaction Reference No.",
                "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "TXN_TYPE",
                    "DATA_TYPE": "string",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Transaction Type",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "CREATED_AT",
                    "DATA_TYPE": "string",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "CREATED_AT",
                    "DISPLAY_ORDER": 3,
                    settings : 
                    {
                        isInSlot: true,
                    }
                },
                {
                    "COLUMN_NAME": "AMOUNT",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Amount",
                    "DISPLAY_ORDER": 4,
                    settings : 
                    {
                        isInSlot: true,
                    }
                },
                {
                    "COLUMN_NAME": "AFTER_BALANCE",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Balance",
                    "DISPLAY_ORDER": 5,
                    settings : 
                    {
                        isInSlot: true,
                    }
                },
                {
                    "COLUMN_NAME": "Payment_Mode",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Payment Mode",
                    "DISPLAY_ORDER": 6
                   
                }
            ],
            listheaderbuttons: [
                {
                    title: 'BacktoList',
                    iconClass: 'fa fa-angle-double-left',
                    callbackfunction: 'BackToList',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                    checkPrivilege: false
                }
            ],
            leftsearchSchema: ClientWalletTransactionHistory.leftsearchSchema,
             searchCondition: '',
        };
    },
    created() {
        this.Currency = this.GetUserInfo.Currency.replace(/[()]/g, '');
        this.FetchHistoryRecords(this.companyId);
        ClientWalletTransactionHistory.bindleftCommonSearchdropdown(this, 'Transaction_Type');
        this.client_id=this.$route.params.client_id;
        this.parentId=this.$route.params.parentId;
    },
    methods: {
        leftCommonSearch: async function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = encodeURIComponent(value.searchCondition);
            }
            else {
                this.searchCondition = "";
            }
            this.FetchHistoryRecords();
        },
        FetchHistoryRecords() {
            debugger
            this.isLoading = true;
            var User_Client_id= this.$route.params.client_id?this.$route.params.client_id:this.GetUserInfo.ID;
            const url = `sortBy=${this.SortBy}&sortExp=${this.SortExp}&pageSize=${this.PageSize}&pageNum=${this.PageNumber}&client_id=${User_Client_id}&type=ALL&ClientCompanyId=${this.$route.params.id}&reqFrom=clientWallet&search=${this.searchCondition}`;
            DataService.GetCompanyWalletHistory(url)
                .then((response) => {
                    const json_data = JSON.parse(response?.data[0].JSON_RESULT) || [];
                    const data = json_data.JSON_HISTORY;
                    if (data.length > 0) {
                        this.HistoryData = data.map(item => ({
                            ...item,
                            CREATED_AT: item.CREATED_AT ? new Date(item.CREATED_AT) : null
                        }));
                        this.TotalRecords = data[0].TOTAL_RECORDS;
                        this.TotalPages = Math.ceil(this.TotalRecords / this.PageSize);
                        this.CurrentPage = this.PageNumber;
                    } else {
                        this.HistoryData = [];
                        this.TotalRecords = 0;
                    }
                    this.isLoading = false;
            });
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchHistoryRecords();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchHistoryRecords();
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "BackToList":
                    this.BackToList();
                    break;
            }
        },
        BackToList() {
            var vm = this;
            if(vm.client_id>0){
                this.$router.push({name: "UpdateClient",params: { id: vm.client_id,parentId: vm.parentId },query: {redirectFrom: "Wallet" }});
            }else{
                vm.$router.push({ name: 'WalletRecharge' });
            }
        }
    }
};
</script>
<style>
.transaction-list {
    max-height: 500px;
    overflow-y: auto;
    margin-top: 20px;
    padding-right: 15px;
    border-top: 1px solid #ececec;
}

.transaction {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    border-bottom: 1px solid #f1f1f1;
}

.transaction .icon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 22px;
    font-weight: bold;
}

.transaction .icon.green {
    background-color: #16a34a;
}

.transaction .icon.red {
    background-color: #dc2626;
}

.transaction .details {
    flex: 1;
    padding-left: 15px;
}

.transaction .title {
    font-size: 16px;
    font-weight: bold;
}

.transaction .description {
    font-size: 14px;
    color: #777;
}

.transaction .balance {
    font-size: 14px;
    color: #666;
    margin-top: 5px;
}

.transaction .amount {
    font-size: 16px;
    font-weight: bold;
}

.transaction .amount.recharge {
    color: #16a34a;
}

.transaction.amount.deduction {
    color: #dc2626;
}
</style>

